<template>
    <div class="card" style="height: 95%;">
        <div class="card__title">
            <b-row>
            <b-col>
                <span>Receipt From Customer</span>
            </b-col>
            <!-- <b-col>
              <div class='Plan-Header-Bagde'>
              <span> Total : {{M_TotalCustomerAmt}} </span>
              </div>
            </b-col> -->
            <b-col style="max-width: max-content !important;" class="col-right">
                <span>
                  <ABSButton
                    :text="'Print'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doPrint"
                  />
                </span>
                <span>
                  <ABSButton
                    :text="'View All'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="onViewAll"
                  />
                </span>
            </b-col>
            </b-row>
        </div>
        <div class="card__body">
          <div>
              <ACCFormList
                  :prop="propList"
                  :title="''"
                  :cHeader="StaticHeader"
                  @rowClicked="rowClicked"
                  @onRenderData="onRenderData"
                  ref="ref_receipt_from_customer"
              >
                <template slot="outstanding_amount" slot-scope="data">
                  <span>{{isCurrency(data.item.outstanding_amount, decimal)}}</span>
                </template>
              </ACCFormList>
          </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/ST/ST_ReceiptFromCustomer",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "receipt_date DESC,customer_name ASC",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
      StaticHeader: [
        {
          key: "no",
          label: "No",
          tdClass: "ContentACCList2 notranslate",
          thClass: "HeaderACCList2"
        },
        {
          key: "customer_name",
          label: "Customer Name",
          tdClass: "ContentACCList2 notranslate th-cus-left",
          thClass: "HeaderACCList2 S th-cus-center"
        },
        {
          key: "outstanding_amount",
          label: "Outstanding Amount",
          tdClass: "ContentACCList2 notranslate th-cus-right",
          thClass: "HeaderACCList2 S th-cus-center"
        },
      ]
    };
  },
  computed: {},
  methods: {
    rowClicked(record, index) {
        var param = record;
        // param.isPrint = true;
        this.$store.commit("setParamPage", param);
        this.$router.push({ name: "ST_ReceiptFromCustomerView" });
    },
    RenderData() {
      this.$refs.ref_receipt_from_customer.doGetList("");
    },
    doPrint() {
      this.$router.push({ name: "ST_ReceiptFromCustomerPrint" });
    },
    onViewAll() {
      this.$router.push({ name: "ST_ReceiptFromCustomer" });
    },
    onRenderData(data) {
      if (!data) return
    //   if (data[0].total_customer_amount && data[0].total_customer_amount !== '') {
    //     this.M_TotalCustomerAmt = this.isCurrency(data[0].total_customer_amount, this.decimal)
    //   }
    }
  },
  mounted() {
    this.RenderData();
  },
};
</script>

<style scoped>
</style>